.slider-label .item{
    text-align: center;
    -ms-flex-align: end;
        align-items: flex-end;
    width:6.5em;
    position: relative;
}

.slider-label .item .barra{
    position: absolute;
    background: black;
    width: 1px;
    height: 2rem;
    margin: 1rem 0 0 0;
    left: 50%;
    display: none;
}

.slider-label .item:nth-child(1) .barra,
.slider-label .item:nth-child(5) .barra
{
    width: 2px;
    height: 2.5rem;
    margin: .8rem 0 0 -1px;
}


